import { ADD_CARD } from '../actions/card.actions'

const initialState = {
  status: 'idle',
  cards: [],
  newCounter: 0
};

let cardsTemp

const card = (state = initialState, action) => {
  switch (action.type) {
    case ADD_CARD:
      cardsTemp = [...state.cards]
      cardsTemp[action.payload.ticker] = action.payload
      state.cards = cardsTemp
      return state
    default:
      return state
  }
}

export default card


